<template>
    <div>
        <div>
            <input type="file" name="mypic" accept=".jpg, .jpeg, .png" @change="uploadAvatar">
        </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
  data() {
    return {};
  },
  methods: {
    uploadAvatar(avatar) {
        console.log(avatar.target.files[0])
        let file = avatar.target.files[0]
      let data = new FormData();
      data.append("file", file, file.name);//很重要 data.append("file", file);不成功
      data.append('data',112)
      console.log(data.get('file'))
      return axios.post("uploadFile", data, {
        headers: { "content-type": "multipart/form-data" }
      });
    }
    }
};
</script>



