
<template>
    <div>
        <mt-header title="实名认证" fixed class="mt-header wid">
            <router-link to="Myhome" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>

        <div class="ab-tit">照片示例：</div>
        <div class="card">
            <img src="img/card.jpg" alt="">
        </div>
        <hr>
        <div class="waring">
            &nbsp;<h4>注意:</h4>
            <p>&nbsp;&nbsp;上传身份证正面照：字迹必须清晰，请严格按照示例照片上传，如果证件照片信息有误，封号。</p>
        </div>
        <hr>
        <form action="uploadFile?did=0"  method="POST" enctype="multipart/form-data" @submit='uploadimg'>
            <input type="file" name="mypic" class="upload" id='img'>
            <br>
            <br>
            <input type="submit" value="上传照片" class="submit">
        </form>
        <!-- <div>
            <input type="file" name="mypic" class="upload" id="img">
            <br>
            <input type="button" value="上传照片" class="submit" @click="uploadimg">
        </div> -->
    </div>
</template>
<script>

import {Toast} from 'mint-ui'
export default {
    data(){ return {  } },
    methods:{
        uploadimg(){
            let img=document.getElementById('img').files[0]

            if(img){
                Toast('提交成功！')
            }else{
                e.preventDefault()
                Toast('请选择要上传的照片！')
            }
            // let x=document.getElementById('img').files[0];
            // console.log(x);
            // let params = new FormData() ; //创建一个form对象
            // params.append('file',x);  //append 向form表单添加数据
            // //添加请求头  通过form添加的图片和文件的格式必须是multipart/form-data
            // let config = {
            //     headers:{'Content-Type':'multipart/form-data'}
            // }
            // console.log(params);
            // this.axios.post("uploadFile",params,config)
            //     .then((result)=>{
            //     console.log(result);
            // })
        }
    }
}
</script>


<style>
    form{
        padding: 0 10px;
    }
    .submit{
        margin-left: 70%;
    }
    .card>img{
        width: 80%;
        display: block;
        margin: 0 auto;
    }
    .waring{
        padding:0 10px;
    }
    .waring>p{
        color: rgb(165,42,42)
    }
</style>








